<template>
<div class="page-buy">
    <div class="price-part">
        <span>商品总价：</span>
        <span class="price">￥{{$store.getters.price}}</span>
    </div>
    <div class="btns">
        <span class="btn-cancel" @touchend="$router.back()">取消购买</span>
        <span class="btn-buy" @touchend="buyItem">立即购买</span>
    </div>
    <h2 class="list-title">购买商品</h2>
    <Product v-for="item in $store.state.products" :key="item._id" :data="item"></Product>`
</div>
</template>
<style lang="scss">
.page-buy {
    .price-part {
        background-color: #fff;
        padding: 30px 10px 10px;
        font-size: 18px;
        .price {
            font-size: 30px;
            color: #f30;
        }
    }
    .btns {
        background-color: #fff;
        text-align: center;
        padding: 15px 0 30px;
        span {
            padding: 5px 10px;
            font-size: 12px;
            color: #fff;
            background: #aaa;
            border-radius: 5px;
            margin: 0 10px;
        }
        .btn-buy {
            background-color: #f30;
        }
    }
    .list-title {
        background-color: #fff;
        padding: 10px;
        font-size: 24px;
        font-weight: normal;
    }
}
</style>
<script>
// 引入组件
import Product from '@/components/Product'
export default {
    // 注册组件
    components: { Product },
    // 创建完成
    created() {
        // 没有商品
        if (!this.$store.state.products.length) {
            // 返回上一个页面
            this.$router.back();
        }
    },
    // 方法
    methods: {
        // 购买商品
        buyItem() {
            // 向服务器端发送请求
            this.$http 
                .post('/static/data/buy.json', { ids: this.$store.state.products.map(item => item._id) })
        }
    }
}
</script>